import { sync } from 'resolve';
import * as path from 'path';

/* tslint:disable:export-name */
export function resolvePackage(packageName: string, rootPath: string): string {
/* tslint:enable:export-name */
  return path.dirname(sync(packageName, {
    basedir: rootPath,
    packageFilter: (pkg: { main: string }): { main: string } => {
      pkg.main = 'package.json';
      return pkg;
    }
  }));
}
